import React, {useState} from "react";

import { Button, Container, Row, Col } from "reactstrap";

import ProjectInfoList from "assets/json/proyects.json";
import ProjectModal from "./ProjectModal";

const projectInfo = Object.freeze(ProjectInfoList);

var currentInd = 6;

function Projects() {

  const intialProjs = projectInfo.slice(0, currentInd).map( (project, i) => {
    return (
      <Col key={i} sm="6" md="4" className="p-5">
        <ProjectModal  project={project} className={"modal-lg project-" + i}/>
      </Col>
    )
  } ) 

  const [projArr, setProjArr] = useState(intialProjs);

  const showMore = () => {
    currentInd += 3
    if(currentInd >= projectInfo.length - 1)
      setActiveButton("");
    let newArr = projectInfo.slice(0, currentInd);
    const proyectsToLoad = newArr.map( (project, i) => {
      return (
        <Col key={i} sm="6" md="4" className="p-5">
          <ProjectModal  project={project} className={"modal-lg project-" + i}/>
        </Col>
      )
    } );
    return setProjArr(proyectsToLoad);
  }

  const [activeButton, setActiveButton] = useState(
    <Button color="default" outline onClick={showMore} className="btn-round mx-auto">
      More Projects!
    </Button>
  );

  return (
    <>
      <div className="section" data-background-color="black">
        <Container className="text-center">
          <Row>
            <h3 className="category p-3">Some of my projects!</h3>
          </Row>
          <Row>
            {projArr}
          </Row>
          <Row>
            {activeButton}
          </Row>
        </Container>
      </div>
    </>
  );
}

export default Projects;
