import React, { useState } from "react";

import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

function ProjectModal(props) {
  const { className } = props;

  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  const links = props.project.technologies.map((tech, i) => {
    return (
      <li key={i}>
        <i className={tech.icon}></i> {tech.name}
      </li>
    );
  });

  const linkButton =
    props.project.projectUrl !== "" ? (
      <a
        color="default"
        className="btn btn-link h1"
        href={props.project.projectUrl}
        target="_blank"
        rel="noopener noreferrer"
      >
        <i className="fas fa-link"></i>
      </a>
    ) : (
      ""
    );

  const notAvailable =
    props.project.projectUrl === "" && props.project.git === "" ? (
      <p className="text-muted">LINKS NOT AVAILABLE</p>
    ) : (
      ""
    );

  const gitButton =
    props.project.git !== "" ? (
      <a
        color="default"
        className="btn btn-link h1"
        href={props.project.git}
        target="_blank"
        rel="noopener noreferrer"
      >
        <i className="fab fa-github"></i>
      </a>
    ) : (
      ""
    );

  const window1 =
    props.project.window1 !== "" ? (
      <img
        alt="..."
        className="rounded img-raised apear-up"
        style={{
          display: "block",
          margin: "auto",
          width: "20%",
          position: "absolute",
          left: "10%",
          top: "10%"
        }}
        src={props.project.window1}
      />
    ) : (
      ""
    );

  const window2 =
    props.project.window2 !== "" ? (
      <img
        alt="..."
        className="rounded img-raised apear-up"
        style={{
          display: "block",
          margin: "auto",
          width: "25%",
          position: "absolute",
          right: "15%",
          top: "20%",
          animationDelay: "200ms"
        }}
        src={props.project.window2}
      />
    ) : (
      ""
    );

  return (
    <div>
      <div className="d-flex justify-content-center">

          <Button
            onClick={toggle}
            alt="..."
            className="rounded img-raised project-image"
            style={{
                backgroundImage: `url(${props.project.thumbnail})`,
            }}
          >
              <div 
                className="btn-round shadowed"
                color="primary"
                onClick={toggle}
                style={{ display: "block", margin: "auto", backgroundColor: "white", color: "#2CA8FF" }}>
                See more!
              </div>
          </Button>

      </div>

      <Modal isOpen={modal} toggle={toggle} className={className}>
        <ModalHeader toggle={toggle}>
          <span className="h3 category">{props.project.name}</span>
        </ModalHeader>
        <ModalBody>
          <img
            alt="..."
            style={{ display: "block", margin: "auto", width: "60%" }}
            src={props.project.imgHeader}
          ></img>
          {window1}
          {window2}

          <div className="section p-4">
            <p className="text-justify"
              dangerouslySetInnerHTML={{ __html: props.project.description }}
            />
            <h2 className="h4 category">Technologies used</h2>
            <ul className="list">{links}</ul>
          </div>

          <div className="d-flex justify-content-center">
            {notAvailable}
            {linkButton} &nbsp; {gitButton}
          </div>
        </ModalBody>
        <ModalFooter></ModalFooter>
      </Modal>
      <br />
      <h5>{props.project.name}</h5>
    </div>
  );
}

export default ProjectModal;
