import React from "react";

import Techs from "assets/json/techs.json"

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  TabContent,
  TabPane,
  Container,
  Row,
  Col
} from "reactstrap";

const iconsStyle = {
    margin : "0.5rem"
}

function Technollogies() {
  let [shouldAnimate, setShlouldAnimate] = React.useState(false);

  let animateClass = shouldAnimate ? "apear-up": "";

  React.useEffect(() => {
      const listenr = () => {
          let quoteElem = document.getElementsByClassName("main")[0];
          let elemPos = quoteElem.offsetTop - 300;
          if(window.pageYOffset >= elemPos && window.pageYOffset) {
            console.log({elemPos})
            console.log(document.getElementsByClassName("main")[0].offsetTop);
            
              setShlouldAnimate(true);
          }
      }
      window.addEventListener("scroll", listenr);
      return function cleanup() {
          window.removeEventListener("scroll", listenr);
      }
    });

  const [iconPills, setIconPills] = React.useState("1");
  const techsList = Object.freeze(Techs);
  const frontTechs = techsList[0].techs.map( (item, i) => {
    return (
        <Col key={i} xs="6" md="4">
            <h2 className="category" style={iconsStyle}><i className={item.icon}></i></h2>
            <p>{item.name}</p>
        </Col> 
    )
  } )
  const backTechs = techsList[1].techs.map( (item, i) => {
    return (
        <Col key={i} xs="6" md="4">
            <h2 className="category" style={iconsStyle}><i className={item.icon}></i></h2>
            <p>{item.name}</p>
        </Col> 
    )
  } )
  const otherTechs = techsList[2].techs.map( (item, i) => {
    return (
        <Col key={i} xs="6" md="4">
            <h2 className="category" style={iconsStyle}><i className={item.icon}></i></h2>
            <p>{item.name}</p>
        </Col> 
    )
  } )
  return (
    <>
      <div className="section">
        <Container>
          <Row>
            <Col className="ml-auto px-5" sm="0" md="4">
              <br/>
              <br/>
              <img src={require('assets/img/encoding.svg')} alt=""/>
            </Col>
            <Col className={`mr-auto ${animateClass}`} sm="10" md="8" id="techs-id" style={{transitionDelay: "1.4s"}}>
              <p className="category">My stack of technollogies</p>
              <Card>
                <CardBody>
                  <Row>
                    <Col sm="3">
                      <Button
                        className="btn-link"
                        color={iconPills === "1" ? "info" : "default"}
                        type="button"
                        onClick={e => {
                            e.preventDefault();
                            setIconPills("1");
                          }}
                      >
                        <i className="fas fa-paint-brush"></i>
                        &nbsp;Front End
                      </Button>

                      <Button
                        className="btn-link"
                        color={iconPills === "2" ? "info" : "default"}
                        type="button"
                        onClick={e => {
                            e.preventDefault();
                            setIconPills("2");
                          }}
                      >
                        <i className="fas fa-cogs"></i>
                        &nbsp;Back End
                      </Button>

                      <Button
                        className="btn-link"
                        color={iconPills === "3" ? "info" : "default"}
                        type="button"
                        onClick={e => {
                            e.preventDefault();
                            setIconPills("3");
                          }}
                      >
                        <i className="fas fa-toolbox"></i>
                        &nbsp;Other techs
                      </Button>
                    </Col>

                    <Col sm="9">
                      <TabContent
                        className="text-center"
                        activeTab={"iconPills" + iconPills}
                      >
                        <TabPane tabId="iconPills1">
                            <Row>
                                {frontTechs}
                            </Row>
                        </TabPane>
                        <TabPane tabId="iconPills2">
                            <Row>
                                {backTechs}
                            </Row>
                        </TabPane>
                        <TabPane tabId="iconPills3">
                            <Row>
                                {otherTechs}
                            </Row>
                        </TabPane>
                      </TabContent>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
}

export default Technollogies;
