/*eslint-disable*/
import React from "react";

// reactstrap components
import { Container, Row, Col } from "reactstrap";
// core components

function IndexHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth > 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop * 2 + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <div className="page-header clear-filter" filter-color="blue">
      <div
        className="page-header-image"
        style={{
          backgroundImage: "url(" + require("assets/img/scenic-view.jpg") + ")"
        }}
        ref={pageHeader}
      ></div>
      <Container>
        <div className="content-center brand" style={{top: "45%"}}>
          <img
            alt="..."
            className="n-logo"
            src={require("assets/img/logofinal.png")}
          ></img>
          <h1 className="h1-seo header-title my-5">Hola mundo!</h1>
          <h3 className="header-subtitle card code my-5">
            <span className="function">.soy</span>(
            <span className="string">"Vusumzi Belmont"</span>);
          </h3>
          <br/>
          <br/>
          <Row>
            <Col xs="4">
              <h1 className="text-center">5+</h1>
              <p className="text-center">años de experiencia</p>
            </Col>
            <Col xs="4">
              <h1 className="text-center">50+</h1>
              <p className="text-center">proyectos realizados</p>
            </Col>
            <Col xs="4">
              <h1 className="text-center">25+</h1>
              <p className="text-center">tecnologías dominadas</p>
            </Col>
          </Row>
          <i className="fas fa-sort-down header-title scrolling-arrow"></i>
        </div>
      </Container>
    </div>
  );
}

export default IndexHeader;
