import React, {useState} from 'react';
import { Container, Row, Col } from 'reactstrap';

function Quote(){
    let [shouldAnimate, setShlouldAnimate] = useState(false);

    let animateClass = shouldAnimate ? "apear-up": "";

    React.useEffect(() => {
        const listenr = () => {
            let quoteElem = document.getElementById("quote-id");
            let elemPos = quoteElem.offsetTop;
            if(window.pageYOffset >= elemPos && window.pageYOffset) {
                setShlouldAnimate(true);
            }
        }
        window.addEventListener("scroll", listenr);
        return function cleanup() {
            window.removeEventListener("scroll", listenr);
        }
      });

    return (
        <div className="section" id="quote-id">
            <Container>
                <Row>
                    <Col md="10" className="mx-auto">
                        <div className={`card p-5 ${animateClass}`} style={{transitionDelay: "1.4s"}}>
                            <blockquote>
                                <p>"when you are programming you are teaching possibly the stupidest thing in the entire universe, a computer, how to do something"</p>
                                <p className="h5 text-right">-Gabe Newell</p>
                            </blockquote>
                        </div>
                    </Col>
                </Row>
            </Container>
            
        </div>
    )
}

export default Quote;